import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {AppRoutingModule} from './app.routing.module';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpModule} from '@angular/http';
import {BookService, BooksEditComponent, BookComponent, BooksDetailComponent, BookAddComponent} from './books/index';
import {AppComponent} from './app.component';

@NgModule({
imports: [BrowserModule, AppRoutingModule, FormsModule, ReactiveFormsModule, HttpModule],
  declarations: [AppComponent, BookComponent, BookAddComponent, BooksDetailComponent, BooksEditComponent],
  providers: [BookService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



