import {Component, OnInit} from '@angular/core';
import {Book} from './book';
import {BookService} from './books.service';
import {Http} from '@angular/http';
import {Router} from '@angular/router';

const UPDATE_URL = 'https://guarded-escarpment-87523.herokuapp.com/books/update';

@Component({
  selector: 'app-edit-book',
  templateUrl: './books.edit.component.html',
})

export class BooksEditComponent implements OnInit {

  updateBook: Book;

  constructor(private bookservice: BookService, private route: Router, private http: Http) {
  }

  ngOnInit(): void {
    this.updateBook = this.bookservice.selectedBook;
  }

  onSubmit() {
    console.log('EditComponent: onSubmit() methode');
    console.log(this.updateBook);

    this.http.post(UPDATE_URL, this.updateBook).map(data => data.json()).subscribe();
    this.route.navigate(['books']);
  }


  getButtonStyle() {
    return {
      'cursor': 'not-allowed'
    };
  }
}



