import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {BooksEditComponent, BookComponent, BooksDetailComponent, BookAddComponent} from './books/index';

const routes: Routes = [
  {path: '', redirectTo: '/books', pathMatch: 'full'},
  {path: 'books', component: BookComponent},
  {path: 'addbook', component: BookAddComponent},
  {path: 'detail', component: BooksDetailComponent},
  {path: 'edit', component: BooksEditComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})

export class AppRoutingModule {
}



